.itemsList {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.item, .itemRegion {
  width: 50%;
  box-shadow: inset 0 -1px 0 0 var(--color-blue-grey);
  border: 1px solid var(--color-light-blue);
  border-radius: 4px;
  padding: 5px 10px;
  margin-bottom: 1px;
  font-size: calc(14px + var(--device-wv));
}
.item:hover {
  background-color: var(--color-light-blue);
}
.title, .titleRegion {
  text-align: center;
  font-size: calc(18px + var(--device-wv));
}
.index {
  font-size: 11px;
}
.filters {
  display: flex;
}
/* Details */
.titleDetails {
  font-size: calc(22px + var(--device-wv));
  background-color: var(--color-light-blue);
  border-radius: 4px;
  padding: 0 10px;
  box-shadow: 2px 2px 1px var(--color-blue-line);
  text-shadow: 2px 2px 1px var(--color-blue-line);
  margin-top: 0;
}
.linkCities, .countryFilter {
  display: inline-block;
  border-radius: 4px;
  font-size: calc(10px + var(--device-wv));
  color: var(--color-dark-blue);
  font-weight: bold;
  background-color: var(--color-light-blue);
  box-shadow: 2px 2px 1px var(--color-blue-line);
  cursor: pointer;
  margin-bottom: 10px;
  padding: 5px 5px 5px 10px;
}
.countryFilter {
  display: inline-block;
  color: var(--color-red);
  padding: 1px 5px 1px 10px;
  margin-left: 10px;
}
.linkCities:hover, .countryFilter:hover {
  background-color: var(--color-blue);
  color: var(--color-white);
}
.flagFilter {
  font-size: 24px;
}
.flagBlock {
  display: inline-block;
  padding: 0 10px;
  margin-left: 10px;
}
.flag {
  font-size: 80px;
  vertical-align: middle;
}
.flagSmall {
  font-size: 20px;
  vertical-align: middle;
  margin: -5px 0 0 7px;
}
.info {
  border: 1px solid var(--color-light-blue);
  border-radius: 4px;
  padding: 10px;
  font-size: 14px;
  box-shadow: 2px 2px 1px var(--color-blue-line);
}
.infoItem {
  border-bottom: 1px solid var(--color-light-blue);
  padding: 5px;
}
.infoItemTitle {
  display: inline-block;
  color: var(--color-black-light);
  min-width: 150px;
}
.message {
  font-size: 12px;
  color: var(--color-red);
  text-align: center;
  margin-top: 10px;
}

.iconWikiData, .iconWiki, .iconGoogleMaps, .iconPhone, .iconCurrency, .iconCity, .iconRight, .iconCloseFilter {
  position: relative;
  vertical-align: middle;
  margin: -5px 0;
}
.iconWikiData {
  font-size: 25px;
  top: -2px;
}
.iconWiki {
  font-size: 22px;
  top: -2px;
  left: 1px;
}
.iconGoogleMaps {
  font-size: 20px;
  top: -3px;
  left: 3px;
}
.iconPhone {
  font-size: 20px;
  position: relative;
  top: -2px;
}
.iconCity {
  font-size: 20px;
  top: -2px;
  margin-right: 5px;
}
.iconRight {
  font-size: 15px;
  top: -2px;
  margin-left: 3px;
}
.iconCloseFilter {
  font-size: 20px;
  margin-left: 5px;
}

.currencyCode {
  font-size: 11px;
  border: 1px solid var(--color-dark-blue);
  border-radius: 3px;
  padding: 1px 5px 1px 0;
  margin-right: 10px;
}
.iconCurrency {
  font-size: 22px;
  top: -2px;
  left: -1px;
  margin-left: -8px;
}
/* Regions */
.wrapperRegions {
  border: solid 1px var(--color-blue-line);
  border-radius: 4px;
  padding: 10px;
  font-size: 14px;
  margin-top: 20px;
  box-shadow: 1px 2px 1px var(--color-blue-line);
}
.titleRegion {
  font-style: italic;
  font-size: calc(13px + var(--device-wv));
}
.itemRegion {
  font-size: calc(11px + var(--device-wv));
}

